@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&Inconsolata:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{padding:env(safe-area-inset)}body{background-color:#f6e0e0;box-sizing:border-box;color:#464646;font-family:Raleway,sans-serif;width:100%}button{background-color:inherit;border:none;color:#464646;font-size:1.5em;opacity:0.7}button:focus{opacity:0.3;outline:none}#app{-webkit-align-items:center;align-items:center;background:linear-gradient(90deg, rgba(159,213,101,0.6) 0%, rgba(213,101,103,0.6) 45%);border-radius:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;left:50%;padding:0.5rem;position:absolute;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}#app #length-control{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between}#app #length-control div{font-size:1.2em;line-height:1em;margin:0em;padding:0;text-align:center;width:8rem}#app #length-control div input{background-color:inherit;border:none;display:inline-block;font-family:Inconsolata,monospace;font-size:1.3em;max-width:2rem;padding:0.3em;text-align:center}#app #timer-controls-wrapper{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}#app #timer-controls-wrapper #timer{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;height:16rem;-webkit-justify-content:center;justify-content:center;margin:1em 0;position:relative;text-align:center;width:16rem}#app #timer-controls-wrapper #timer #timer-svg{-webkit-transform:scaleX(-1);transform:scaleX(-1)}#app #timer-controls-wrapper #timer #timer-svg #timer-circle{fill:none;stroke:none}#app #timer-controls-wrapper #timer #timer-svg #timer-circle #timer-circle-outter,#app #timer-controls-wrapper #timer #timer-svg #timer-circle #timer-circle-inner{stroke:rgba(255,255,255,0.5);stroke-width:0.3rem}#app #timer-controls-wrapper #timer #timer-svg #timer-circle #timer-circle-inner-rem,#app #timer-controls-wrapper #timer #timer-svg #timer-circle #timer-circle-outer-rem{stroke-linecap:round;stroke-width:0.3rem;-webkit-transform:rotate(90deg);transform:rotate(90deg);-webkit-transform-origin:center;transform-origin:center;transition:1s linear all}#app #timer-controls-wrapper #timer #timer-text{position:absolute}#app #timer-controls-wrapper #timer #timer-text #timer-label{font-size:1.5em}#app #timer-controls-wrapper #timer #timer-text #time-left{font-family:Inconsolata,monospace;font-size:3em;margin-top:0}@media screen and (min-width: 560px){#app{-webkit-flex-direction:row;flex-direction:row}}

